	module.exports = "<h2 id=overview>Overview</h2> <p>A tooltip is used to provide contextual information for another component on hover. The tooltip itself is made up of two elements: arrow and content. One of the directional modifiers (<code>.pf-m-left</code>, <code>.pf-m-top</code>, etc.) is required on the tooltip component.</p> <h2 id=accessibility>Accessibility</h2> <table> <thead> <tr> <th>Attribute</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>role=&quot;tooltip&quot;</code></td> <td><code>.pf-c-tooltip</code></td> <td>Adds a tooltip role to the tooltip component. <strong>Required</strong></td> </tr> <tr> <td><code>aria-describedby=&quot;[id of .pf-c-tooltip__content]&quot;</code> or <code>aria-labelledby=&quot;[id of .pf-c-tooltip__content]&quot;</code></td> <td><code>[element that triggers the tooltip]</code></td> <td>Makes the contents of the tooltip accessible to assistive technologies by associating the tooltip text with the element that triggers the tooltip. Use <code>aria-labelledby</code> if the tooltip provides a label for the element. Use <code>aria-describedby</code> if the element already has an accessible label that is different from the tooltip text. <strong>Required</strong></td> </tr> </tbody> </table> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-c-tooltip</code></td> <td><code>&lt;div&gt;</code></td> <td>Creates a tooltip. Always use with a modifier class that positions the tooltip relative to the element it describes. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-c-tooltip__arrow</code></td> <td><code>&lt;div&gt;</code></td> <td>Creates an arrow pointing towards the element the tooltip describes. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-c-tooltip__content</code></td> <td><code>&lt;div&gt;</code></td> <td>Creates the body of the tooltip. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-m-left</code></td> <td><code>.pf-c-tooltip</code></td> <td>Positions the tooltip to the left of the element.</td> </tr> <tr> <td><code>.pf-m-right</code></td> <td><code>.pf-c-tooltip</code></td> <td>Positions the tooltip to the right of the element.</td> </tr> <tr> <td><code>.pf-m-top</code></td> <td><code>.pf-c-tooltip</code></td> <td>Positions the tooltip to the top of the element.</td> </tr> <tr> <td><code>.pf-m-bottom</code></td> <td><code>.pf-c-tooltip</code></td> <td>Positions the tooltip to the bottom of the element.</td> </tr> </tbody> </table> ";

